export const Login = params => {
  return axios.post("/api2/user/login", params, {
    headers: {}
  });
};
// 默认匿名登录，然后用户主动登录时调用接口更新信息
export const updateLogin = (params) => {
  return axios.post("/api/user/up", params);
}
// 每次打开主页统计一次
export const countAccess = (uid, source, refby) => {
  if(!uid) return {};
  let origin = "https://m.lichat.me";
  if(/http/gi.test(window.location.protocol)) origin = "http://localhost:7007";
  let url = origin + "/web/vfun/countAccess";
  return axios.post(url, {uid, source, refby});
}

export const HomeList = (ruid, page = 1, size = 20) => {
  let query = { };
  let token = localStorage.getItem("token") || "";
  if (!token || token.trim().length < 1) {
    if(ruid) query.uid = ruid;
  }
  // let url = "/vFun/user/list";
  let url = "/api/user3/list";
  return axios.post(url, {
    pageSize: size,
    query: query,
    pageNum: page
  });
};

