export const QueryBalance = (uid) => {
  if(!uid) uid = JSON.parse(localStorage.getItem("profile") || "{}").uid;
  return axios.post("/api/pay/chat", { relate_uid: uid });
};

export const GetVfunConfig = async () => {
  let url =
    (window.location.protocol === "https"
      ? "https://wwww.livetube.xyz"
      : "http://localhost:5050") + "/api/vfun/getConfig";
  let uid = JSON.parse(localStorage.getItem("profile") || "{}").uid;
  if (!uid) return false;
  let result = {};
  await axios
    .post(url + "?uid=" + uid)
    .then(res => {
      console.log("Server GetVfunConfig => ", res);
      result.data = res.data;
    })
    .catch(error => {
      console.error(error);
    });
  return result;
};

export const TrackSourceFrom = async (uid, source, refby) => {
  let query = { uid, source, refby };
  console.log("TrackParam => ", query);
  return axios.post("/api/tracking", query);
};
