const debug = true;

export const getItem = (key) => {
    let result = localStorage.getItem(key);
    if(debug) console.log(`GetItem: ${key} => ${result}`);
    return result;
}
export const setItem = (key, value) => {
    if(debug) console.log(`SetItem: ${key} => ${value}`);
    return localStorage.setItem(key, value);
}
export const getJson = (key) => {
    let result = {};
    try{
        result = JSON.parse(localStorage.getItem(key) || "{}");
    }catch(err){
        result.error = err;
    }
    if(debug) console.log(`GetJson: ${key} => `, result);
    return result;
}
export const setJson = (key, value) => {
    if(debug) console.log(`SetJson: ${key} => `, value);
    return localStorage.setItem(key, JSON.stringify(value || {}));
}

export const getOtherInfo = () => {
    let OtherInfo = getJson("OtherInfo");
    return OtherInfo;
}
export const setOtherInfo = (value) => {
    return setJson("OtherInfo", value);
}
export const getTrackInfo = () => {
    let OtherInfo = getJson("trackInfo");
    return OtherInfo;
}
export const setTrackInfo = (value) => {
    return setJson("trackInfo", value);
}