<template>
  <div id="app">
    <Connect ref="vConnect" @close="show = false" v-if="show" />
    <keep-alive>
      <router-view v-if="$route.meta.keep">
        <!-- 这里是会被缓存的视图组件，比如 Home！ -->
      </router-view>
    </keep-alive>
    <router-view v-if="!$route.meta.keep">
      <!-- 这里是不被缓存的视图组件，比如 Edit！ -->
    </router-view>
  </div>
</template>
<script>
import Connect from "./components/Connect";
import { mapMutations } from "vuex";
import * as Storage from "@/storage";

export default {
  components: {
    Connect
  },
  data() {
    return {
      show: false
    };
  },
  methods: {
    ...mapMutations(["updateProfile", "setVConnect", "updateTrackInfo"]),
    getUrlparam(key, url) {
      if (!url) url = window.location.href;
      let index = url.indexOf("?");
      if (index < 0) return null;
      url = url.substr(index + 1);
      let arr = url.split("&");
      let result = null;
      arr.forEach(value => {
        let cur = value.split("=");
        if (cur[0] === key) {
          result = cur[1];
          return;
        }
      });
      return result;
    },
    saveTrackInfo() {
      let trackInfo = JSON.parse(localStorage.getItem("trackInfo") || "{}");
      if (trackInfo.source && trackInfo.refby) {
        console.log("Already has trackInfo");
        return;
      }
      let param = this.getUrlparam("param") || "";
      let arr = param.split("-");
      let source, refby;
      for (let i = 0; i < arr.length; i++) {
        let cur = arr[i].split("_");
        if (cur[0] === "source") source = cur[1];
        else if (cur[0] === "refby") refby = cur[1];
      }
      trackInfo.source = source;
      trackInfo.refby = refby;
      let appId = 305;
      if (source && source.length > 1) appId = 306;
      trackInfo.appId = appId;
      this.updateTrackInfo(trackInfo);
    },
    getCusParam(){
      let params = this.getUrlparam("cus");
      let arr = params ? params.split("-") : [];
      arr = arr.map((x) => {
        return x.trim();
      });
      return arr;
    },
    handleTrackInfo(source, refby){
      let trackInfo = Storage.getTrackInfo();
      if(trackInfo.source && trackInfo.refby){
        console.log("Already Save TrackInfo. ", trackInfo);
        return;
      }
      trackInfo.source = source;
      trackInfo.refby = refby;
      trackInfo.appId = (source && source.length > 1) ? 306 : 305;
      Storage.setTrackInfo(trackInfo);
    },
    handleUrlParam(){
      let arr = this.getCusParam();
      console.log("HandleUrlParam: ", arr);
      let region = arr[0];
      let source = arr[1];
      let refby  = arr[2];
      // handle trackInfo
      if (source && refby) {
        console.log("Handle track info");
        this.handleTrackInfo(source, refby);
      }
      // handle region
      if(region){
        console.log("handle otherinfo");
        let OtherInfo = Storage.getOtherInfo();
        if(OtherInfo.region !== region){
          OtherInfo.region = region;
          Storage.setOtherInfo(OtherInfo);
        }
      }
      return arr;
    }
  },
  created() {
    const local = localStorage.getItem("profile");
    if (local) {
      this.updateProfile(JSON.parse(local));
    }
    let arr =  this.handleUrlParam();
    if(!arr || arr.length < 1) this.saveTrackInfo();
  },
  mounted() {
    this.setVConnect(this.$refs.vConnect);
  }
};
</script>
<style lang="less">
#app {
  // background-color: #262626;
  background-color: #fff;
  // display: flex;
  // flex-direction: column;
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
}
