<template>
  <div class="recharge-page" id="recharge_page">
    <div class="left-bar" @click="onClickLeft">
      <van-icon name="cross" />
    </div>
    <!--  -->
    <div class="content-block available-coins" v-if="false">
      <h1>Available Coins</h1>
      <div class="balance-container">
        <img class="star-icon" :src="require('@/assets/icon/star-icon.png')" />
        <span class="balance">{{ profile.diamond }}</span>
      </div>
    </div>
    <!--  -->
    <div class="content-block diamond-list" v-if="false">
      
    </div>
    <!-- user info -->
    <div class="user-info" v-if="showUserInfo">
      <div class="info-item">
        <span>UID: </span> <span> {{ profile.uid }} </span>
      </div>
      <div class="info-item">
        <span>NickName: </span> <span> {{ profile.nickname }} </span>
      </div>
    </div>
    <!--  -->
    <h2 class="recharge-tips"> {{ strings.rechargeTips }} </h2>
    <!-- PaymentMethods -->
    <div class="payments" id="payments">
      <div class="pay-methods" v-show="!showStripePopup">
        <div class="pay-method paypal"
          @click="startPay(curProduct.productIosId, newPayMethods[1].payParams)" >
          <h3 class="paypal"><span class="with"> Pay with </span></h3>
          <img class="pay-icon" :src="newPayMethods[1].icon" />
          <h3 class="paypal"><span class="pay">Pay</span><span class="pal">Pal</span></h3>
        </div>
        <div class="pay-method stripe"
          @click="startPay(curProduct.productIosId, newPayMethods[0].payParams)" >
          <img class="stripe" :src="require('@/assets/stripe-button.png')" />
        </div>
      </div>
      <!-- stripe -->
      <div v-show="showStripePopup"
        class="stripe-popup" >
        <form id="payment-form" v-if="!stripeRechargeSuccess">
          <div id="card-element"><!--Stripe.js injects the Card Element--></div>
          <button id="submit">
            <div class="spinner hidden" id="spinner"></div>
            <span id="button-text">Pay</span>
          </button>
          <p class="success-nohide" id="card-error" role="alert"></p>
          <p class="result-message hidden success-nohide">
            {{ strings.stripe1 }}
            <a :href="stripePaySuccessLink" target="_blank"> {{ strings.stripe2 }} </a>
            {{ strings.stripe3 }}
          </p>
        </form>

        <div class="success-tips" v-if="stripeRechargeSuccess">
          <p>
            {{ strings.stripe1 }}
            <a :href="stripePaySuccessLink" target="_blank"> {{ strings.stripe2 }} </a>
          </p>
          <p>{{ strings.stripe3 }}</p>
        </div>
      </div>
    </div>
    <!-- PayPal -->
    <van-popup
      v-model="showPaypalPopup"
      class="paypal-popup"
      closeable
      close-icon-position="top-left"
      @close="closePayPage"
    >

      <iframe :src="PayPalUrl"></iframe>
    </van-popup>
    <!--  -->
    <van-overlay :show="showLoading" class="loading-overlay">
      <van-loading size="24px" color="#1989fa" vertical>Loading...</van-loading>
    </van-overlay>
  </div>
</template>

<script>
import Vue from "vue";
import { Popup, Button,Toast } from "vant";
import * as Server from "@/server";
import { mapState, mapMutations } from "vuex";
import { getStrings } from '../strings';

let stripeKey = "pk_live_Zt23ptBqWWJ8as6k2MBUF3xn00XviuaLpZ";
const stripe = window.Stripe(stripeKey);

Vue.use(Popup)
  .use(Button)
  .use(Toast);
export default {
  data() {
    return {
      strings: getStrings(),
      dialog: false,
      products: [],
      curProduct: {},
      showUserInfo: false,
      showRechargePopup: false,
      showStripePopup: false,
      stripeRechargeSuccess: false,
      stripePaySuccessLink: "",
      useNewMethods: false,
      PayPalUrl: null,
      showPaypalPopup: false,
      showLoading: false,
      balance: 0,
      payMethods: [
        {
          payway: "9",
          name: "PAYTM",
          icon: "https://lichat.me/pay_way_paytm826.png",
          payParams: "paytm_in",
        },
        {
          payway: "11",
          name: "PhonePe",
          icon: "https://lichat.me/pay_way_phonepe1123.png",
          payParams: "wallet_in",
        },
        {
          payway: "8",
          name: "UPI",
          icon: "https://lichat.me/pay_way_upi0826.png",
          payParams: "upi_in",
        },
        {
          payway: "10",
          name: "EBanking",
          icon: "https://lichat.me/pay_way_ebanking826.png",
          payParams: "ebanking_in",
        },
      ],
      newPayMethods: [
        {
          name: "Stripe",
          icon: require("@/assets/icon/stripe-icon.png"),
          payParams: "stripe",
        },
        {
          name: "PayPal",
          icon: require("@/assets/icon/paypal-icon.png"),
          payParams: "paypal",
        }
      ],
    };
  },
  computed: {
    ...mapState(["profile"]),
  },
  created() {
    if (this.$route.query.token) {
      localStorage.setItem("token", this.$route.query.token);
    }
    this.loadProfile();
    // let url = window.location.href;
    // let uid = JSON.parse(localStorage.getItem("profile") || "{}").uid;
    // if (/showInfo/.test(url) || (uid && uid.toString().substr(0, 3) === "306"))
      // this.showUserInfo = true;
  },
  methods: {
    ...mapMutations(["updateProfile"]),
    async sleep(duration){
      if(!duration) duration = 500;
      return new Promise(resolve => setTimeout(resolve, duration));
    },
    loadPayMethods(profile) {
      // let trackInfo = JSON.parse(localStorage.getItem("trackInfo") || "{}");
      // if(trackInfo.appId !== 305)
      // if (profile.uid.toString().substr(0, 3) !== "305")
      //   this.payMethods = this.newPayMethods;
      console.log("LoadPayMethods : ", profile);
    },
    async loadProfile() {
      // let { data, status } = await Server.Profile();
      // if (status !== 0) {
      //   console.log("LoadProfile Failed => ", data);
      //   return;
      // }
      // this.updateProfile(data);
      // localStorage.setItem("profile", JSON.stringify(data));
      // this.loadPayMethods(data);
      this.loadPayMethods();
    },
    async loadData() {
      // const { data, status } = await Server.ProductList();
      // if (status === 0) {
      //   this.products = data;
      // }
      this.curProduct = {
        diamond: 200,
        money: 1.99,
        productIosId: "com.vfun.200d"
      }
    },
    async diamondHandler(item) {
      console.log("Diamond handler > ", item);
      this.curProduct = item;
      this.showRechargePopup = true;
    },
    async startPay(productIosId, code) {
      this.showLoading = true;
      if (code === "paypal") {
        let { data, status } = await Server.PayByPaypal(productIosId);
        console.log(`StartPay => ${status} + `, data);
        if (status !== 0) return;
        this.showPaypalPopup = true;
        this.PayPalUrl = data;
        this.showRechargePopup = false;
      } else if (code === "stripe") {
        console.log("stripe pay");
        this.showRechargePopup = false;
        this.showStripe(productIosId);
      } else {
        let { data, status } = await Server.PayByUrl(productIosId, code);
        console.log(`StartPay => ${status} + }`, data);
        if (status !== 0) return;
        window.location.href = data.payUrl;
      }
      this.showLoading = false;
    },
    async closePayPage(num) {
      this.showLoading = true;
      await this.sleep(5000);
      let { status, data } = await Server.QueryBalance();
      this.balance = status === 0 ? data : 0;
      console.log("Close PayPage - ", num, data);
      if(this.balance > 0) this.$toast(this.strings.rechargeSuccess);
      this.showLoading = false;
      // this.$toast("If successful recharge, refresh the page");
      // setTimeout(this.loadProfile, 1000);
    },
    onClickLeft() {
      // this.$router.go(-1);
      // this.$router.push("/home");
      this.$emit("leftRecharge", this.balance);
      this.showStripePopup = false;
      Toast.clear();
    },
    async showStripe(productId) {
      this.showStripePopup = true;
      if (this.stripeRechargeSuccess) {
        console.log("Again use stripe, need refresh the page.");
        return;
      }
      var that = this;
      let { status, data } = await Server.PayByStripe(productId);
      if (status !== 0) return;

      var elements = stripe.elements();
      var style = {
        base: {
          color: "#32325d",
          fontFamily: "Arial, sans-serif",
          fontSmoothing: "antialiased",
          fontSize: "16px",
          "::placeholder": {
            color: "#32325d",
          },
        },
        invalid: {
          fontFamily: "Arial, sans-serif",
          color: "#fa755a",
          iconColor: "#fa755a",
        },
      };
      var card = elements.create("card", { style: style });

      // Show a spinner on payment submission
      var loading = function (isLoading) {
        if (isLoading) {
          // Disable the button and show a spinner
          document.querySelector("#submit").setAttribute("disabled", "true");
          document.querySelector("#spinner").classList.remove("hidden");
          document.querySelector("#button-text").classList.add("hidden");
        } else {
          document.querySelector("#submit").removeAttribute("disabled");
          document.querySelector("#spinner").classList.add("hidden");
          document.querySelector("#button-text").classList.remove("hidden");
        }
      };
      /* ------- UI helpers ------- */
      // Shows a success message when the payment is complete
      var orderComplete = function (paymentIntentId) {
        loading(false);
        // setTimeout(that.loadProfile, 1000);
        that.stripeRechargeSuccess = true;
        that.stripePaySuccessLink =
          "https://dashboard.stripe.com/test/payments/" + paymentIntentId;
        document.querySelector(".result-message").classList.remove("hidden");
        document.querySelector("#submit").setAttribute("disabled", "true");
        that.closePayPage(1);
      };
      // Show the customer the error from Stripe if their card fails to charge
      var showError = function (errorMsgText) {
        loading(false);
        var errorMsg = document.querySelector("#card-error");
        errorMsg.textContent = errorMsgText;
        setTimeout(function () {
          errorMsg.textContent = "";
        }, 5000);
      };

      var payWithCard = function (stripe, card, clientSecret) {
        loading(true);
        stripe
          .confirmCardPayment(clientSecret, {
            payment_method: {
              card: card,
            },
          })
          .then(function (result) {
            if (result.error) {
              // Show error to your customer
              showError(result.error.message);
            } else {
              // The payment succeeded!
              orderComplete(result.paymentIntent.id);
              that.showStripePopup = false;
            }
          });
      };

      card.mount("#card-element");
      card.on("change", function (event) {
        // Disable the Pay button if there are no card details in the Element
        // document.querySelector("#submit").disabled = event.empty;
        if (event.empty) {
          document.querySelector("#submit").setAttribute("disabled", "true");
        } else document.querySelector("#submit").removeAttribute("disabled");
        document.querySelector("#card-error").textContent = event.error
          ? event.error.message
          : "";

        var form = document.getElementById("payment-form");
        form.addEventListener("submit", function (event) {
          event.preventDefault();
          // Complete payment when the submit button is clicked
          payWithCard(stripe, card, data);
        });
      });
    },
  },
  mounted() {
    this.loadData();
  }
};
</script>

<style lang="less">
iframe {
  border: none;
  width: 100%;
  height: 100%;
}
.left-bar {
  height: 50px;
  line-height: 50px;
  font-size: 16px;
  color: #2626268a;
  cursor: pointer;
}
.van-dialog {
  width: 90%;
}
.content-block {
  padding: 20px;
  border-radius: 10px;
}
.content-block + .content-block {
  margin-top: 5px;
}
.available-coins,
.choose-your-plan {
  text-align: center;
  background: linear-gradient(
    to top right,
    rgb(255, 0, 122),
    rgb(255, 1, 47),
    rgb(255, 0, 122)
  );
  color: #fff;

  h1 {
    font-size: 20px;
    margin: 0;
  }

  h1 + * {
    margin-top: 15px;
  }

  .balance-container {
    display: flex;
    align-items: center;
    justify-content: center;

    .star-icon {
      max-height: 20px;
      margin-right: 5px;
    }

    .balance {
      font-size: 20px;
      font-weight: bold;
      color: #fff;
    }
  }
}

.user-info {
  padding: 10px;
  background: #ffe6ea40;
  margin-top: 10px;
  border-radius: 10px;

  .info-item {
    font-size: 10px;
    color: #999;
    font-weight: 500;
    *:first-child {
      opacity: 0.7;
    }
  }
}

.diamond-list {
  background: rgb(255, 230, 234);

  .diamond-item {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  .diamond-item + .diamond-item {
    margin-top: 30px;
  }

  .diamond-preview {
    height: 20px;
    display: flex;
    align-items: flex-end;
  }
  .diamond-icon {
    max-height: 20px;
    margin-right: 5px;
  }
  .diamond-count {
    font-size: 18px;
    line-height: 20px;
    color: #000;
  }

  .diamond-money {
    color: #000;
    font-size: 18px;
    line-height: 20px;
  }

  .bonus-diamond {
    font-size: 14px;
    margin: 10px 0 0 0;

    span {
      color: #f00;
    }
  }

  .recharge-button {
    background: linear-gradient(to right, rgb(255, 0, 122), rgb(255, 1, 47));
    font-size: 16px;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 30px;
    padding: 0 10px 0 20px;
    border-radius: 20px;

    .diamond-money {
      color: #fff;
      font-size: 14px;
    }

    img {
      margin-left: 10px;
      max-height: 15px;
    }
  }
}

.recharge-page {
  height: 100%;
  max-height: 100vh;
  overflow-y: scroll;
  padding: 0px 20px 20px 20px;
  color: #000;
  position: relative;

  .balance-wrap {
    width: 315px;
    margin: 0 auto;
    height: 144px;
    background: linear-gradient(
      90deg,
      rgba(107, 125, 252, 1) 0%,
      rgba(244, 93, 130, 1) 100%
    );
    border-radius: 5px;
    position: relative;

    .title {
      font-size: 16px;
      font-weight: 600;
      line-height: 20px;
      color: #fff;
      position: absolute;
      top: 13px;
      left: 15px;
    }
    .value-wrap {
      text-align: center;
      position: absolute;
      left: 50%;
      bottom: 32px;
      transform: translateX(-50%);
      .value {
        font-weight: bold;
        font-size: 36px;
        line-height: 43px;
        color: #fff;
      }
      .tips {
        font-size: 12px;
        color: #fff;
        line-height: 20px;
      }
    }
  }
  .title-tips {
    margin-top: 16px;
    font-size: 14px;
    color: #b5b5b5;
    line-height: 20px;
    padding: 16px 0;
  }
  .recharge-list {
    & + .recharge-list {
      margin-top: 38px;
    }
    .list-title {
      font-size: 14px;
      line-height: 20px;
    }
    .list-items {
      .item {
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size: 14px;
        font-weight: 500;
        padding: 8px 0;

        .label {
          display: flex;
          align-items: center;
          .van-icon {
            margin-right: 5px;
          }
          .hot-label {
            font-size: 16px;
            // color: rgb(266,78,110);
            color: #000;
          }
          .hot-tag {
            background-color: #dd3d2e;
            color: #fff;
            border-radius: 5px;
            height: 18px;
            align-items: center;
            padding: 0 5px;
            font-size: 14px;
            font-weight: bold;
            margin-left: 10px;
            display: none;
          }
          .sale-money {
            color: #000;
          }
        }

        &.hot .hot-tag {
          display: inline-flex;
        }
      }
    }
  }
  //
  .recharge-tips {
    font-size: 18px;
    font-family: Robote;
    color: #262626;
    font-weight: 700;
    margin: 10px 0 20px 0;
  }
  //
  .payments {
    position: relative;
    min-height: 150px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    .pay-methods {
      display: grid;
      z-index: 1;
      grid-template: 1fr;
      gap: 10px;
      & > .stripe {
        height: auto;
      }
      & > .paypal {
        height: 50px;
        background: rgb(245, 205, 85);
        border-radius: 8px;
      }
      .pay-method {
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        transition: all 0.4s ease-in-out;
        &:hover {
          opacity: 0.95;
        }
        img.pay-icon {
          max-width: 30px;
          margin: 0 5px;
        }
        img.stripe {
          width: 100%;
        }
        h3 {
          margin: 0;
          font-size: 19px;
          color: #FFF;
          font-weight: 700;
        }
        h3.paypal > .with {
          color: #262626;
        }
        h3.paypal > .pay {
          color: rgb(11, 47, 147);
        }
        h3.paypal > .pal {
          color: rgb(83, 163, 223);
        }
      }
    }
  }

  .stripe-popup {
    box-sizing: border-box;
    * {
      box-sizing: border-box;
    }
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    background: #fff;

    .cancel-stripe {
      width: 200px;
      font-size: 16px;
      color: #999;
      line-height: 20px;
      border-radius: 20px;
      text-align: center;
      box-shadow: 0 0 1px 0 #999;
      cursor: pointer;
    }

    .success-tips {
      width: 100%;
      margin: 5px auto;

      p {
        font-size: 16px;
        font-weight: 700;
        text-align: center;
        a {
          color: #596fd6;
        }
      }
    }

    form {
      width: 100%;
      align-self: center;
      box-shadow: 0px 0px 0px 0.5px rgba(50, 50, 93, 0.1),
        0px 2px 5px 0px rgba(50, 50, 93, 0.1),
        0px 1px 1.5px 0px rgba(0, 0, 0, 0.07);
      border-radius: 7px;
    }
    input {
      border-radius: 6px;
      margin-bottom: 6px;
      padding: 12px;
      border: 1px solid rgba(50, 50, 93, 0.1);
      height: 44px;
      font-size: 16px;
      width: 100%;
      background: white;
    }
    .result-message {
      line-height: 22px;
      font-size: 16px;
    }
    .result-message a {
      color: rgb(89, 111, 214);
      font-weight: 600;
      text-decoration: none;
    }
    .hidden {
      display: none;
    }
    #card-error {
      color: rgb(105, 115, 134);
      text-align: left;
      font-size: 13px;
      line-height: 17px;
      margin-top: 12px;
    }
    #card-element {
      border-radius: 4px 4px 0 0;
      padding: 12px;
      border: 1px solid rgba(50, 50, 93, 0.1);
      height: 44px;
      width: 100%;
      background: white;
    }
    #payment-request-button {
      margin-bottom: 32px;
    }
    /* Buttons and links */
    button {
      background: #5469d4;
      color: #ffffff;
      font-family: Arial, sans-serif;
      border-radius: 0 0 4px 4px;
      border: 0;
      padding: 12px 16px;
      font-size: 16px;
      font-weight: 600;
      cursor: pointer;
      display: block;
      transition: all 0.2s ease;
      box-shadow: 0px 4px 5.5px 0px rgba(0, 0, 0, 0.07);
      width: 100%;
    }
    button:hover {
      filter: contrast(115%);
    }
    button:disabled {
      opacity: 0.5;
      cursor: default;
    }
    /* spinner/processing state, errors */
    .spinner,
    .spinner:before,
    .spinner:after {
      border-radius: 50%;
    }
    .spinner {
      color: #ffffff;
      font-size: 22px;
      text-indent: -99999px;
      margin: 0px auto;
      position: relative;
      width: 20px;
      height: 20px;
      box-shadow: inset 0 0 0 2px;
      -webkit-transform: translateZ(0);
      -ms-transform: translateZ(0);
      transform: translateZ(0);
    }
    .spinner:before,
    .spinner:after {
      position: absolute;
      content: "";
    }
    .spinner:before {
      width: 10.4px;
      height: 20.4px;
      background: #5469d4;
      border-radius: 20.4px 0 0 20.4px;
      top: -0.2px;
      left: -0.2px;
      -webkit-transform-origin: 10.4px 10.2px;
      transform-origin: 10.4px 10.2px;
      -webkit-animation: loading 2s infinite ease 1.5s;
      animation: loading 2s infinite ease 1.5s;
    }
    .spinner:after {
      width: 10.4px;
      height: 10.2px;
      background: #5469d4;
      border-radius: 0 10.2px 10.2px 0;
      top: -0.1px;
      left: 10.2px;
      -webkit-transform-origin: 0px 10.2px;
      transform-origin: 0px 10.2px;
      -webkit-animation: loading 2s infinite ease;
      animation: loading 2s infinite ease;
    }
    @-webkit-keyframes loading {
      0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
      }
      100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
      }
    }
    @keyframes loading {
      0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
      }
      100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
      }
    }
  }
  // PayPal
  .paypal-popup {
    width: 100%;
    height: 100%;
    background: #fff;

    iframe {
      border: none;
      outline: none;
      width: 100%;
    }
  }

  // loading-overlay
  .loading-overlay {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
</style>
